<template>
	<view>
		<view class="topImageView" :style="'height:' + topHeight + 'px;'">
			<view class="nav row" :style="'margin-top:' + marginTopHeight + 'px;'">
				<text class="nav_title">{{i18n.tabBar.order}}</text>
			</view>
			<view class="row" style="width: 100%;height: 60px;">
				<view v-for="(item,index) in tabbar" :key="index" class="tab_nor" @click="tabbarClick(index)">
					<view>{{item}}</view>
					<image v-show="index == tabIndex" class="content_img" src="../../static/homeuparrow@2x.png"
						mode="aspectFit"></image>
				</view>
			</view>
		</view>
		<block v-if="islogin">
			<swiper style="width: 100%;background-color: #f5f6f7;" :style="'height:' + scrollHeight + 'px;'"
				@animationfinish="animationfinish" :current="tabIndex">
				<swiper-item>
					<postOrderListCell ref="waitPay" :height="scrollHeight" :typeString="0" @cellItemClick="cellItemClick">
					</postOrderListCell>
				</swiper-item>
				<swiper-item>
					<postOrderListCell ref="waitReceive" :height="scrollHeight" :typeString="1" @cellItemClick="cellItemClick">
					</postOrderListCell>
				</swiper-item>
				<swiper-item>
					<postOrderListCell ref="song" :height="scrollHeight" :typeString="2" @cellItemClick="cellItemClick">
					</postOrderListCell>
				</swiper-item>
				<swiper-item>
					<postOrderListCell ref="done" :height="scrollHeight" :typeString="3" @cellItemClick="cellItemClick">
					</postOrderListCell>
				</swiper-item>
				<swiper-item>
					<postOrderListCell ref="refund" :height="scrollHeight" :typeString="4" @cellItemClick="cellItemClick">
					</postOrderListCell>
				</swiper-item>
			</swiper>
			<view class="add" @click="add()">
				<image src="../../static/neworder/addorder@2x.png" mode="aspectFit"></image>
			</view>
		</block>
		<block v-else>
			<view class="logintip" @click="login()">{{i18n.mine.logintip}}</view>
		</block>

	</view>
</template>

<script>
	import postOrderListCell from '../../components/postOrderListCell/postOrderListCell.vue'
	var amapFile = require('@/libs/amap-wx.130.js'); //如：..­/..­/libs/amap-wx.js
	var myAmapFun = new amapFile.AMapWX({
		key: getApp().globalData.amapKey
	});
	export default {
		components: {
			postOrderListCell
		},
		data() {
			return {
				scrollHeight: 0,
				tabbar: [],
				islogin: true,
				tabIndex: 0,
				curAdsMsg: {
					name: '',
					desc: ''
				},
				topHeight:0,
				marginTopHeight:0
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			var res = uni.getSystemInfoSync()
			this.marginTopHeight = getApp().globalData.menuButtonHeight + getApp().globalData.statusBarHeight
			this.topHeight = this.marginTopHeight + 44 + 60 
			this.scrollHeight = res.windowHeight - this.topHeight
			this.tabbar = [this.i18n.postorder.nopay,
				this.i18n.postorder.noreceive,
				this.i18n.postorder.doing,
				this.i18n.postorder.done,
				this.i18n.postorder.cancel
			]
		},
		onShow() {
			var value = uni.getStorageSync('userinfo')
			if (value && value.uid > 0) {
				this.islogin = true
				this.$refs.waitPay.updateListData('1')
				this.$refs.waitReceive.updateListData('2')
				this.$refs.song.updateListData('3')
				this.$refs.done.updateListData('4')
				this.$refs.refund.updateListData('5')
			} else {
				this.islogin = false
			}
		},
		onLoad() {
			// uni.$on('changloginstatus',(param) => {
			// 	console.log(22)
			// 		if(param == '1'){
			// 			this.islogin = true
			// 		}else{
			// 			this.islogin = false
			// 		}		
			// 	})
			
		},
		onUnload() {

		},
		methods: {
			cellItemClick(item, index) {
				if(item.status==3 || item.status==4){
					uni.navigateTo({
						url:'../../package-publishOrder/pages/order-map-detaile?ID=' + item.id
					})
				}else{
					uni.navigateTo({
						url: '../../package-publishOrder/pages/postOrderDetail?ID=' + item.id
					})
				}
			},
			add() {
				uni.navigateTo({
					url: '../../package-publishOrder/pages/newOrder'
				})
			},

			tabbarClick(index) {
				this.tabIndex = index
			},
			animationfinish(e) {
				this.tabIndex = e.detail.current
			},
			gotoCell(item) {
				
			},
			login() {
				uni.navigateTo({
					url: '../login/login'
				})
			}

		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #fcfcfc;
	}

	.logintip {
		width: 120px;
		height: 40px;
		border-radius: 20px;
		background-color: #00C1DE;
		color: white;
		font-size: 16px;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		margin-top: calc(50% - 40px);
		margin-left: calc(50% - 60px);
	}

	.add {
		position: absolute;
		width: 50px;
		height: 50px;
		right: 15px;
		bottom: 60px;
		/* background-color: red; */
	}

	.add image {
		width: 50px;
		height: 50px;
	}

	.topImageView {
		width: 100%;
		overflow: hidden;
		background-color: #2F3E51;
		/* position: relative; */
		background-size: 100% auto;
	}

	.nav {
		display: flex;
		align-items: center;
		height: 44px;
		border-bottom: 1px solid #383838;
	}

	.nav_title {
		width: 50%;
		margin-left: 25%;
		height: 20px;
		line-height: 20px;
		color: white;
		font-size: 16px;
		text-align: center;
		
	}

	.content_img {
		position: absolute;
		left: calc(50% - 6.5px);
		bottom: 0;
		width: 13px;
		height: 8px;
	}

	.tab_sel {
		font-size: 16px;
		font-weight: bold;
		color: white;
		width: 20%;
		height: 100%;
		display: flex;
		flex-direction: column;
		/* margin-left: 15px;
		margin-right: 4px; */
		position: relative;
		/* height: 30px; */
		justify-content: center;
		align-items: center;
		

	}

	/* .tab_sel::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 25%;
		height: 2px;
		border-radius: 1px;
		width: 50%;
		background: url('@/static/home-down-z.png');
	} */

	.tab_nor {
		font-size: 15px;
		/* font-weight: bold; */
		color: white;
		width: 20%;
		height: 100%;
		display: flex;
		flex-direction: column;
		position: relative;
		justify-content: center;
		align-items: center;
	}
</style>
